@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300;500;700&display=swap);
html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input,
select {
  vertical-align: middle;
}

body {
  background-color: rgb(22, 22, 22);
  color: #e0e0ee;
}
.App {
  font-family: "Inconsolata";
  text-align: center;
}

.d_row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  grid-gap: 0.5em;
  gap: 0.5em;
  grid-column-gap: 0.4em;
  -webkit-column-gap: 0.4em;
          column-gap: 0.4em;
  justify-content: center;
  align-items: center;
  width: 95%;
  font-weight: bolder;
  font-size: 12pt;
  margin-inline: auto;
  margin-bottom: 1.5em;
  margin-top: 1em;
}

h1.status-bar {
  margin-block: 1em;
  font-size: 12pt;
}

.price-chart {
  display: flex;
  flex-direction: column;
  margin-inline: auto;
  width: 80%;
  max-width: 30rem;
  align-items: space-between;
}

table tbody tr td {
  font-size: 14pt;
  padding: 0.4em 0em;
}
table tbody tr {
  margin-bottom: 1em;
}

table thead tr th {
  font-size: 15pt;
  font-weight: bold;
  padding: 0.5em 0.2em;
}

.item-name {
  text-align: left;
}
.item-price {
  text-align: center;
}

thead tr th {
  padding-bottom: 0.4em;
  /* display: none; */
}
.sell-btn {
  padding: 0.5em 0em;
  border-radius: 100%;
  cursor: pointer;
}

.buy-btn {
  padding: 0.5em 0em;
  border-radius: 100%;
  cursor: pointer;
}

/* location */
.btn-hidden {
  display: none;
}

/* Alert Box */
/* The alert message box */
.alert-hidden {
  /* padding: 1em; */
  height: 0%;
  opacity: 0%;
  width: 80%;
  max-width: 30rem;
  margin-inline: auto;
  margin-bottom: 1em;
  border-radius: 5px;
  background-color: rgb(32, 32, 32);
  color: #ff6464;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 500ms ease-in-out all;
}

.alert {
  width: 80%;
  padding: 1em;
  height: 100;
  opacity: 100;
  max-width: 30rem;
  margin-inline: auto;
  margin-bottom: 1em;
  border-radius: 5px;
  background-color: rgb(32, 32, 32);
  color: #ff6464;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 500ms ease-in-out all;
}

/* The close button */
.closebtn {
  margin-left: 15px;
  color: #ff6464;
  font-weight: bold;
  font-size: 22px;
  line-height: 20px;
  cursor: pointer;
  transition: 0.3s;
}

/* When moving the mouse over the close button */
.closebtn:hover {
  color: #ff2c2c;
}

button {
  border: 2px solid rgb(180, 190, 185);
  margin-inline: 2px;
  border-radius: 5px;
  background-color: rgb(22, 22, 22);
  color: #e0eee2;
  padding: 0.4em 0.8em;
  font-size: 11pt;
  font-weight: bold;
  cursor: pointer;
}

#qty-box {
  max-width: 3em;
  background-color: rgb(22, 22, 22);
  outline: none;
  border: none;
  color: #e0e0ee;
}

.slidecontainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 15rem;
  margin-inline: auto;
  justify-content: center;
  align-items: center;
  grid-gap: 0.5em;
  gap: 0.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 0.4em;
  border: 1px solid rgb(121, 121, 121);
  background-color: rgb(32, 32, 32);
  padding: 1em 0em;
  z-index: 2;
}

.slidecontainer-hidden {
  display: none;
}

.btn-container {
  display: flex;
  grid-gap: 1em;
  gap: 1em;
}
.btn-container button {
  font-size: 12pt;
}

.slider {
  width: 13rem;
}

p.prompt-name {
  font-size: 15pt;
  font-weight: 900;
}

.confirm-box {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 15rem;
  margin-inline: auto;
  justify-content: center;
  align-items: center;
  grid-gap: 0.5em;
  gap: 0.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 0.4em;
  border: 1px solid rgb(121, 121, 121);
  background-color: rgb(32, 32, 32);
  padding: 1em 0em;
  z-index: 2;
}

.confirm-box .btns {
  display: flex;
  margin-top: 0.3em;
  grid-gap: 0.5em;
  gap: 0.5em;
}

.confirm-box p {
  font-size: 15pt;
  font-weight: 900;
}

.confirm-box-hidden {
  display: none;
}

.dialog-box {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 19rem;
  margin-inline: auto;
  justify-content: center;
  align-items: center;
  grid-gap: 0.5em;
  gap: 0.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 0.4em;
  border: 1px solid rgb(121, 121, 121);
  background-color: rgb(32, 32, 32);
  padding: 1em 0em;
  z-index: 2;
}
.debt-dialog-box {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 20rem;
  margin-inline: auto;
  justify-content: center;
  align-items: center;
  grid-gap: 0.5em;
  gap: 0.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 0.4em;
  border: 1px solid rgb(121, 121, 121);
  background-color: rgb(32, 32, 32);
  padding: 1em 0em;
  z-index: 2;
}

.dialog-box .btns,
.debt-dialog-box .btns {
  display: flex;
  margin-top: 0.3em;
  grid-gap: 0.5em;
  gap: 0.5em;
}

.dialog-box p,
.debt-dialog-box p {
  font-size: 15pt;
  font-weight: 900;
}

.dialog-box-hidden {
  display: none;
}
.debt-dialog-box-hidden {
  display: none;
}

table tbody tr td.item-name,
table tbody tr td.item-price {
  cursor: pointer;
  border-radius: 0.2em;
  padding: 0em 0.2em;
}

table tbody tr td.item-name:hover,
table tbody tr td.item-price:hover {
  background-color: #2c2c2c;
}

.btn-disabled {
  border: 2px solid grey;
  color: grey;
  cursor: not-allowed;
}

.low {
  color: rgb(86, 173, 86);
}
.avg {
  color: inherit;
}
.high {
  color: rgb(211, 84, 84);
}

